/* hide reveal button in MS Edge */
input::-ms-reveal {
    display: none;
}

.swal2-container {
    z-index: 150000 !important;
}

.swal2-actions button {
    min-width: 100px !important;
}

.swal-toast-margin-top {
    margin-top: 60px;
}

.swal-toast-success {
    margin-top: 60px;
}

.swal-toast-success .swal2-title {
    margin-top: 60px;
    color: #00BFA5 !important;
}

.swal-toast-error {
    margin-top: 60px;
    color: #E45570 !important;
}

.swal-toast-center-margin {
    margin-top: 0px;
    color: #E45570 !important;
}

.dataGrid-header {
    background-color: #00BFA5;
}

.navBox {
    display: block;
    width: 100%;
    padding: 5px;
    padding-top: 10px;
    text-align: center;
    color: #000;
}

.navBox:hover {
    background-color: #00BFA5;
    color: #FFF;
    cursor: pointer
}

.navActive {
    background-color: #00BFA5;
    color: #FFF
}

.MuiGridPanelContent-root {
    max-height: 200px !important;
}